import { Icon, Popover } from "antd-mobile";
import { PopoverItem } from "./proxyComponents";
import React, { useState } from "react";

export type RightMoreMenuProps = {
    onItemClick: (key: string) => any;
    // @ts-ignore
    items: PopoverItem[];
};

export function RightMoreMenu(
    {
        items,
        onItemClick,
    }: RightMoreMenuProps
) {
    const [isRightMenuVisible, setIsRightMenuVisible] = useState(false);

    function handleRightMenuSelect(item: any) {
        onItemClick(item.key)
    }

    return (
        <Popover
            key="0"
            mask
            visible={isRightMenuVisible}
            overlay={items}
            align={{
                overflow: { adjustY: 0, adjustX: 0 }
            }}
            onVisibleChange={() => setIsRightMenuVisible(!isRightMenuVisible)}
            onSelect={handleRightMenuSelect}
        >
            <div style={{
                height: "100%",
                padding: "0 15px",
                marginRight: "-15px",
                display: "flex",
                alignItems: "center"
            }}
            >
                <Icon type="ellipsis"/>
            </div>
        </Popover>
    );
}
