import React from "react";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { Icon, NavBar } from "antd-mobile";
import { withRouter } from "react-router";
import { ModuleRootRoutePath, PoliceWriteMessagePageRoutePath } from "../lazyPages";
import { PageScreen } from "../components/PageScreen";
import { PopoverItem } from "../components/proxyComponents";
import { RightMoreMenu } from "../components/RightMoreMenu";
import { TabPanel, TabPanelItem } from "../components/TabPanel";
import { PolicePublicMessage } from "./PolicePublicMessage";


export function HomePage(
    {
        history
    }: any
) {

    function handleRightMenuSelect(menuKey) {
        if (menuKey === "write") {
            history.push(PoliceWriteMessagePageRoutePath);
        }
        console.log("handle menu click", menuKey);
    }

    const rightMenu = (
        <RightMoreMenu
            key="0"
            items={[
                (<PopoverItem key="write">签写书信</PopoverItem>),
                (<PopoverItem key="login">登录</PopoverItem>)
            ]}
            onItemClick={handleRightMenuSelect}
        />
    );

    const tabs: TabPanelItem[] = [
        { title: "信息公开", renderTabContent: () => <PolicePublicMessage /> },
        { title: "信件", renderTabContent: () => "tab" },
        { title: "平台规定", renderTabContent: () => "tab" }
    ];

    return (
        <PageScreen>
            <NavBar
                icon={<Icon type="left"/>}
                onLeftClick={() => history.replace(ModuleRootRoutePath)}
                rightContent={[
                    rightMenu
                ]}
            >
                警情沟通
            </NavBar>

            <TabPanel
                sessionKey="PoliceHome"
                items={tabs}
            />
        </PageScreen>
    );
}

export default compose(
    hot(module),
    withRouter
)(HomePage);
